import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);
//组件
const Rescue = () => import('@pages/Rescue');
const Error = () => import('@pages/Error');
const Complete = () => import('@pages/Complete');

const router = new VueRouter({
  mode: 'history', //不使用哈希路由
  routes: [
    {
      path: '/',
      redirect: '/rescue'
    },
    {
      path: '/rescue',
      name: 'rescue',
      meta: {
        title: '急救定位'
      },
      component: Rescue
    },
    {
      path: '/error',
      name: 'error',
      meta: {
        title: '急救定位'
      },
      component: Error
    },
    {
      path: '/complete',
      name: 'complete',
      meta: {
        title: '急救定位'
      },
      component: Complete
    },
    // 没有匹配到路由重定向到,注意要放到最后
    {
      path: "*",
      redirect: "/rescue"
    }
  ]
});

export default router;