/*------------------------------------------注册键盘事件-----------------------------------*/
//解决ie兼容性问题
//1、keydown() keydown事件会在键盘按下时触发.
//2、keyup事件会在按键释放时触发,也就是你按下键盘起来后的事件
//3、keypress()事件会在敲击按键时触发,我们可以理解为按下并抬起同一个按键
//isAddDel是增加还是删除时间, eventType为事件的类型,callBack为回调,useCapture穿透
function keyboard(isAddDel, eventType, callBack, useCapture) {
    callBack = callBack || function () {
        dir('请传入一个函数')
    }
    var isIE = getBrowser().name == 'ie'
    switch (eventType) {
        case 0:
            if (isIE) {
                isAddDel && document.attachEvent('keydown', callBack, useCapture);
                isAddDel || document.detachEvent('keydown', callBack, useCapture);
                return;
            }
            isAddDel && document.addEventListener('keydown', callBack, useCapture);
            isAddDel || document.removeEventListener('keydown', callBack, useCapture);
            break;
        case 1:
            if (isIE) {
                isAddDel && document.attachEvent('keyup', callBack, useCapture);
                isAddDel || document.detachEvent('keyup', callBack, useCapture);
                return;
            }
            isAddDel && document.addEventListener('keyup', callBack, useCapture);
            isAddDel || document.removeEventListener('keyup', callBack, useCapture);
            break;
        case 2:
            if (isIE) {
                isAddDel && document.attachEvent('keypress', callBack, useCapture);
                isAddDel || document.detachEvent('keypress', callBack, useCapture);
                return;
            }
            isAddDel && document.addEventListener('keypress', callBack, useCapture);
            isAddDel || document.removeEventListener('keypress', callBack, useCapture);
            break;
    }

};
/*------------------------------------------注册键盘事件-----------------------------------*/

/*------------------------------------------判断浏览器和获取版本-----------------------------------*/
//直接调用返回一个对象,分别是浏览器名和浏览器版本,如果是ie在cdn引入polyfills,但必须要传入callback使用;
function getBrowser(callBack) {
    callBack = callBack || function () { }
    var browserName = '';
    var ua = navigator.userAgent.toLowerCase(); //获取用户端信息
    var info = {
        ie: /trident/.test(ua) && !/opera/.test(ua), //匹配IE浏览器
        op: /opera/.test(ua), //匹配Opera浏览器
        sa: /version.*safari/.test(ua), //匹配Safari浏览器
        ch: /chrome/.test(ua), //匹配Chrome浏览器
        ff: /gecko/.test(ua) && !/webkit/.test(ua) //匹配Firefox浏览器
    };
    for (var key in info) {
        if (info[key]) {
            browserName = key;
            break;
        }
    }
    var version = {
        ie: function () {
            var ua = navigator.userAgent; //获取用户端信息
            var b = ua.indexOf("Trident"); //检测特殊字符串“MSIE”的位置
            if (b < 0) {
                return 0;
            }
            uf.importScript('https://cdnjs.cloudflare.com/ajax/libs/js-polyfills/0.1.42/polyfill.js', callBack)
            return parseFloat(ua.substring(b + 5, ua.indexOf(";", b))); //截取版本号，并转换为数值
        }, //匹配IE浏览器
        op: function () {
            var ua = navigator.userAgent;
            var b = ua.indexOf("Opera/");
            if (b < 0) {
                return 0;
            }
            return parseFloat(ua.substring(b + 6, ua.lastIndexOf("\.")));
        },
        sa: function () {
            var ua = navigator.userAgent;
            var b = ua.indexOf("Safari/");
            if (b < 0) {
                return 0;
            }
            return parseFloat(ua.substring(b + 7, ua.lastIndexOf("\.")));
        },
        ch: function () {
            var ua = navigator.userAgent;
            var b = ua.indexOf("Chrome/");
            if (b < 0) {
                return 0;
            }
            return parseFloat(ua.substring(b + 7, ua.lastIndexOf("\.")));
        },
        ff: function () {
            var ua = navigator.userAgent;
            var b = ua.indexOf("Firefox/");
            if (b < 0) {
                return 0;
            }
            return parseFloat(ua.substring(b + 8, ua.lastIndexOf("\.")));
        }
    }
    var ver = version[browserName]()
    return {
        name: browserName,
        ver: ver,
    }
}
/*------------------------------------------判断浏览器和获取版本-----------------------------------*/


/*------------------------------------------存储方法总结-----------------------------------------*/
//storageType,表示使用的是localStorage还是使用sessionStorage存储
//methodType表示调用删除还是添加
function storage(storageType, methodType, storageKey, storageValue) {
    if (window.localStorage) return dir('浏览器不支持: localStorage');
    if (window.sessionStorage) return dir('浏览器不支持: sessionStorage')
    if (storageType == 1) {
        switch (methodType) {
            case -1:
                localStorage.removeItem(storageKey);
                break;
            case 0:
                localStorage.clear();
                break;
            case 1:
                localStorage.setItem(storageKey, storageValue);
                break;
            case 1:
                localStorage.getItem(storageKey);
                break;
        }
    } else if (storageType == 2) {
        switch (methodType) {
            case -1:
                sessionStorage.removeItem(storageKey);
                break;
            case 0:
                localStorage.clear();
                break;
            case 1:
                sessionStorage.setItem(storageKey, storageValue);
                break;
            case 2:
                sessionStorage.getItem(storageKey);
                break;
        }
    }
};
/*------------------------------------------存储方法总结-----------------------------------------*/


/*------------------------------------------json转为树形结构-----------------------------------------*/
// var nodes = [
//     { "id": 1, "cname": "第一级1", "ccode": "1" },
//     { "id": 10, "cname": "第一级1", "ipid": 1, "ccode": "1" },
//     { "id": 100, "cname": "第一级2", "ipid": 10, "ccode": "1" },
//     { "id": 1000, "cname": "第一级3", "ipid": 100, "ccode": "1" },
//     { "id": 11, "cname": "第一级3", "ipid": 10, "ccode": "1" },
//     { "id": 11, "cname": "第1", "ipid": 1, "ccode": "1" },
//     { "id": 11, "cname": "第1", "ipid": 1, "ccode": "1" },
// ]
// console.dir(transData(nodes, 'id', 'ipid', 'nodes'))
function transData(list, idstr, pidstr, nodestr) {
    var temp = {},
        result = [],
        tempVp = [],
        parentNodeId = [];
    for (i = 0; i < list.length; i++) {
        temp[list[i][idstr]] = list[i]; //idstr作为key将nodes数组转成对象类型  
    }
    for (j = 0; j < list.length; j++) {
        tempVp = temp[list[j][pidstr]] //遍历数据并且判断当前数据是否有父节点,
        if (tempVp) { //如果遍历的当前数据有父节点
            if (!tempVp[nodestr]) tempVp[nodestr] = []; //如果父元素的nodes对象不存在，则创建数组 
            tempVp[nodestr].push(list[j]); //将遍历的这条数据添加到父节点
        } else {
            result.push(list[j]);
            //list[j]等于第一个for循环的list[i],list[i]赋值给了temp[list[i][idstr]]
            //所以list[j]等于temp[list[i][idstr]],所以result某一项就是temp的其中一个,
            //当循环到下一个时,发现这次循环的数据有父节点,这个父节点就是result中的某一个,
            //通过temp[list[j][pidstr]](temp[list[j][pidstr]]==tempVp,所以把子节
            //点push进tempVp就是push进了result)找到了当前这条数据的父节点,这条父节点恰
            //好存在result中
        }
    }
    return result
};
/*------------------------------------------json转为树形结构-----------------------------------------*/

