//debug版本 https://cdn.bootcss.com/labjs/2.0.3/LAB-debug.min.js
//          https://cdn.bootcss.com/labjs/2.0.3/LAB.min.js
function importScript(url, callBack) {
    var date = (new Date()).valueOf();
    var JSElement = document.createElement("script");
    var head = document.getElementsByTagName('head')[0];
    JSElement.setAttribute("type", "text/javascript");
    url = url.indexOf('?') > 0 ? url + "&version=" + date : url + "?version=" + date;
    JSElement.setAttribute("src", url);
    head.appendChild(JSElement);
    callBack = callBack || function () { };
    JSElement.onload = callBack;
};
//cnd方式js动态引入jq
function importJq(callBack) {
    var url = 'https://code.jquery.com/jquery-3.4.1.min.js';
    uf.importScript(url, callBack)
};
