/*--------------------------------循环函数---------------------------*/
function each(obj, callback) {
    if (uf.judgeDatatype(obj) == 'Array') {
        for (let i = 0; i < obj.length; i++) {
            let item = obj[i];
            let res = callback && callback.call(item, i, item);
            if (res === false) {
                break;
            }
        }
    } else {
        for (let key in obj) {
            if (obj.hasOwnProperty(key)) {
                let value = obj[key];
                let res = callback && callback.call(value, key, value);
                if (res === false) {
                    break;
                }
            }
        }
    }
};
/*--------------------------------循环函数---------------------------*/

/*------------------------------JSON---------------------------------*/
//判断是否是JSON, 是返回JSON, 不是返回false
function isJson(str) {
    if (typeof str == 'string') {
        try {
            var obj = JSON.parse(str);
            if (typeof obj == 'object' && obj) {
                return obj;
            } else {
                return false;
            }
        } catch (e) {
            return false;
        }
    }
};
/*------------------------------JSON---------------------------------*/

/*------------------------------进入全屏-----------------------------*/
//进入全屏,传入dom既可以让某个dom元素实现全屏
function fullScreen(el) {
    var el = document.getElementById(el) || document.body;
    var rfs = el.requestFullScreen || el.webkitRequestFullScreen ||
        el.mozRequestFullScreen || el.msRequestFullScreen;
    if (typeof rfs != "undefined" && rfs) {
        rfs.call(el);
    } else if (window.ActiveXObject != "undefined") {
        //for IE，这里其实就是模拟了按下键盘的F11，使浏览器全屏
        var wscript = new ActiveXObject("WScript.Shell");
        if (wscript != null) {
            wscript.SendKeys("{F11}");
        }
    }
};
/*------------------------------进入全屏-----------------------------*/

/*------------------------------退出全屏-----------------------------*/
function exitFullScreen() {
    var el = document;
    var cfs = el.cancelFullScreen || el.webkitCancelFullScreen ||
        el.mozCancelFullScreen || el.exitFullScreen;
    if (typeof cfs != "undefined" && cfs) {
        cfs.call(el);
    } else if (window.ActiveXObject != "undefined") {
        //for IE，这里和fullScreen相同，模拟按下F11键退出全屏
        var wscript = new ActiveXObject("WScript.Shell");
        if (wscript != null) {
            wscript.SendKeys("{F11}");
        }
    }
};
/*------------------------------退出全屏-----------------------------*/

/*------------------------------图片转换-----------------------------*/
//canvas方法转换base64开始
function getCanvasBase64(option, callBack) {
    if (uf.judgeDatatype(option) != 'Object' || option.url == '') {
        uf.dir('请传入正确的格式')
    }
    option.isShow ? undefined : option.isShow = '';
    option.nodeId ? undefined : option.nodeId = '';
    option.width ? undefined : option.width = '';
    option.height ? undefined : option.height = '';
    getPromise();

    function getPromise() {
        var image = new Image();
        image.crossOrigin = '';
        image.src = option.url;
        if (option.url) {
            new Promise(function (res, rej) {
                image.onload = function () {
                    var base64 = getBase64Image(image, option.imgWidth, option.imgHeight);
                    option.isShow && (document.getElementById(option.nodeId).src = base64);
                    res(base64);
                    rej('error');
                };
            }).then(function (res) {
                callBack(res)
            }).catch(function (res) {
                callBack(res)
            })
        }
    }

    function getBase64Image(img, width, height) {
        var canvas = document.createElement("canvas");
        canvas.width = width ? width : img.width;
        canvas.height = height ? height : img.height;
        var ctx = canvas.getContext("2d");
        ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
        var dataURL = canvas.toDataURL();
        return dataURL;
    };
};
/*------------------------------图片转换-----------------------------*/


/*------------------------------转换方位-----------------------------*/
//根据度数返回文字方位
function angle(angle) {
    angle == 90 ? angle = "正东" : "";
    angle == 180 ? angle = "正南" : "";
    angle == 270 ? angle = "正西" : "";
    (angle == 0 || angle == 360) ? angle = "正北" : "";
    (angle > 0 && angle < 90) ? angle = "北偏东" + angle + "°" : "";
    (angle > 180 && angle < 270) ? angle = "南偏西" + (angle - 180) + "°" : "";
    (angle > 90 && angle < 180) ? angle = "南偏东" + (180 - angle) + "°" : "";
    (angle > 270 && angle < 360) ? angle = "北偏西" + (360 - angle) + "°" : "";
    return angle;
};
/*------------------------------转换方位-----------------------------*/

/*------------------------------数组去重-----------------------------*/
//数组去重,返回新数组
function unique(array) {
    var n = []; //一个新的临时数组
    //遍历当前数组
    for (var i = 0; i < array.length; i++) {
        //如果当前数组的第i已经保存进了临时数组，那么跳过，
        //否则把当前项push到临时数组里面
        if (n.indexOf(array[i]) == -1) n.push(array[i]);
    }
    return n;
};
/*------------------------------数组去重-----------------------------*/

/*------------------------------字符去重-----------------------------*/
//字符串去除空格
function trim(str, type) {
    type || (type = 1); //默认为1去除所有空格
    if (typeof str == 'string') {
        type == 1 ? str = str.replace(/\s+/g, "") : ''; //1 去除所有空格
        type == 2 ? str = str.replace(/^\s+|\s+$/g, "") : ''; //2 去除两端空格
        type == 3 ? str = str.replace(/^\s*/, '') : ""; //3 去除左端空格
        type == 4 ? str = str.replace(/(\s*$)/g, "") : ''; //4 去除右端空格
    }
    return str
};
/*------------------------------字符去重-----------------------------*/

/*----------------------------拷贝一个带有样式的dom节点---------------------------*/
//拷贝一个带有样式的dom节点,第一个参数为需要复制的节点id,第二个参数为将复制的节点复制到这个dom节点上
//有返回值,返回拷贝的样式和html结构
function copyDomNode(copyId, beCopiedId) {
    copyId = document.getElementById(copyId);
    beCopiedId = document.getElementById(beCopiedId);
    copyIdStyles = window.getComputedStyle(copyId);
    beCopiedId.innerHTML = copyId.innerHTML;
    for (var key in copyIdStyles) {
        beCopiedId.style[key] = copyIdStyles[key]
    }
    return { styles: copyIdStyles, html: copyId.innerHTML }
};
/*----------------------------拷贝一个带有样式的dom节点---------------------------*/

/*----------------------------秒转格式时间---------------------------*/
//秒转换为dd-hh--mm-ss等格式
//参数一参数二必传, 其他参数不使用可传入false或不传,取默认
//参数一: 所需要转换的秒
//参数二: 无默认值,只能传入数组,且传入的长度需要和参数三相对应
//参数三: 默认值'dd-hh-mm-ss' 需要转换成的格式, 支持三种格式, 需要修改间隔符号请传入参数三
//        例: 
//        传入'dd-hh-mm-ss'   显示'365 6-6-0', 间隔符号不区分大小写, 不区分':'和'-',不区分空格
//参数四:默认值true,是否启用为00格式,true启用,false禁用 
//        例: 
//        启用 00:00:00 禁用 0:0:0
function secondFormat(second, symbol, type, isAddZero) {
    if (!second || second < 0) return '请传入正确的数值';
    if (!(symbol && symbol.length == 4)) return '请传入正确的间隔符';
    var dd, hh, mm, ss, time, type1, type2, type3;
    type || (type = 'dd-hh-mm-ss');
    isAddZero || (isAddZero = true);
    type = type.toLowerCase().replace(/\s*/g, "").replace(/:/g, '-');
    second = typeof second === 'string' ? parseInt(second) : second;
    dd = second / (24 * 3600) | 0;
    second = Math.round(second) - dd * 24 * 3600; //天
    hh = second / 3600 | 0;
    second = Math.round(second) - hh * 3600; //小时
    mm = second / 60 | 0; //分
    ss = Math.round(second) - mm * 60; //秒
    //是否启用00格式 true 启用 false 不启用
    if (isAddZero) {
        Math.round(dd) < 10 ? (dd = dd > 0 ? '0' + dd : '') : '';
        Math.round(hh) < 10 ? (hh = '0' + hh) : '';
        Math.round(mm) < 10 ? (mm = '0' + mm) : '';
        Math.round(ss) < 10 ? (ss = '0' + ss) : '';
    }
    type1 = hh + symbol[0] + mm + symbol[1] + ss + symbol[2]
    type2 = dd + symbol[0] + hh + symbol[1] + mm + symbol[2]
    type3 = dd + symbol[0] + hh + symbol[1] + mm + symbol[2] + ss + symbol[3]
    type == 'hh-mm-ss' ? time = type1 : (type == 'dd-hh-mm' ? time = type2 : (type == 'dd-hh-mm-ss' ? time = type3 : time = type3));
    return time;
};
/*----------------------------秒转格式时间---------------------------*/
