/*-----------------------------获取根路径----------------------------*/
//获取网站根路径(站点及虚拟目录)/*http://localhost:8080*/
function getRootUr() {
    var strFullPath = window.document.location.href;
    var strPath = window.document.location.pathname;
    var pos = strFullPath.indexOf(strPath);
    var prePath = strFullPath.substring(0, pos);
    var postPath = strPath.substring(0, strPath.substr(1).indexOf('/') + 1);
    //return (prePath + postPath);  //如果发布IIS，有虚假目录用用这句
    return (prePath);
};
/*-----------------------------获取根路径----------------------------*/

/*------------------------------获取URL------------------------------*/
//获取url,域名后,?前,返回字符串
function getUrl(url) {
    url = url ? url : window.location.href;
    url = url.split("//")[1];
    if (url.indexOf("?") > -1) {
        url = url.substr(url.indexOf("/") + 1, url.indexOf("?") - url.indexOf("/") - 1);
    } else {
        url = url.substr(url.indexOf("/") + 1, url.length - url.indexOf("/") - 1);

    }
    return url;
};
/*------------------------------获取URL------------------------------*/

/*-------------------------获取URL中的参数---------------------------*/
//获取url中的参数
//可传入,可获取地址中的,返回对象
function getUrlParmas(url) {
    url = url ? url : window.location.href;
    if (url.indexOf('?') >= 0) url = url.substr(url.indexOf("?") + 1);
    var params = {};
    var arr = url.split("&");
    for (var i = 0; i < arr.length; i++) {
        if (arr[i].indexOf("=") <= 0) continue;
        var o = arr[i].split("=");
        params[o[0]] = o[1];
    }
    return params;
};
/*-------------------------获取URL中的参数---------------------------*/

