/*------------------------------------------百度地图函数开始-----------------------------------------*/


/*-------------------本地原始坐标转其他坐标-----------------*/
function Convertor(ak) {
    this.stepCount = 100;
    this.pointCount = [];
    this.Result = [];
    this.NoisIndex = [];
    this.Time = new Date();
    this.AK = ak;
    this.M_PI = 3.14159265358979324;
    this.A = 6378245.0;
    this.EE = 0.00669342162296594323;
    this.X_PI = this.M_PI * 3000.0 / 180.0;
}
Convertor.prototype.outofChine = function (p) {
    if (p.lng < 72.004 || p.lng > 137.8347) {
        return true;
    }
    if (p.lat < 0.8293 || p.lat > 55.8271) {
        return true;
    }
    return false;
};
Convertor.prototype.WGS2GCJ_lat = function (x, y) {
    var ret1 = -100.0 + 2.0 * x + 3.0 * y + 0.2 * y * y + 0.1 * x * y + 0.2 * Math.sqrt(Math.abs(x));
    ret1 += (20.0 * Math.sin(6.0 * x * this.M_PI) + 20.0 * Math.sin(2.0 * x * this.M_PI)) * 2.0 / 3.0;
    ret1 += (20.0 * Math.sin(y * this.M_PI) + 40.0 * Math.sin(y / 3.0 * this.M_PI)) * 2.0 / 3.0;
    ret1 += (160.0 * Math.sin(y / 12.0 * this.M_PI) + 320 * Math.sin(y * this.M_PI / 30.0)) * 2.0 / 3.0;
    return ret1;
};
Convertor.prototype.WGS2GCJ_lng = function (x, y) {
    var ret2 = 300.0 + x + 2.0 * y + 0.1 * x * x + 0.1 * x * y + 0.1 * Math.sqrt(Math.abs(x));
    ret2 += (20.0 * Math.sin(6.0 * x * this.M_PI) + 20.0 * Math.sin(2.0 * x * this.M_PI)) * 2.0 / 3.0;
    ret2 += (20.0 * Math.sin(x * this.M_PI) + 40.0 * Math.sin(x / 3.0 * this.M_PI)) * 2.0 / 3.0;
    ret2 += (150.0 * Math.sin(x / 12.0 * this.M_PI) + 300.0 * Math.sin(x / 30.0 * this.M_PI)) * 2.0 / 3.0;
    return ret2;
};
Convertor.prototype.WGS2GCJ = function (poi) {
    if (this.outofChine(poi)) {
        return;
    }
    var poi2 = {};
    var dLat = this.WGS2GCJ_lat(poi.lng - 105.0, poi.lat - 35.0);
    var dLon = this.WGS2GCJ_lng(poi.lng - 105.0, poi.lat - 35.0);
    var radLat = poi.lat / 180.0 * this.M_PI;
    var magic = Math.sin(radLat);
    magic = 1 - this.EE * magic * magic;
    var sqrtMagic = Math.sqrt(magic);
    dLat = (dLat * 180.0) / ((this.A * (1 - this.EE)) / (magic * sqrtMagic) * this.M_PI);
    dLon = (dLon * 180.0) / (this.A / sqrtMagic * Math.cos(radLat) * this.M_PI);
    poi2.lat = poi.lat + dLat;
    poi2.lng = poi.lng + dLon;
    return poi2;
};
Convertor.prototype.GCJ2BD09 = function (poi) {
    var poi2 = {};
    var x = poi.lng,
        y = poi.lat;
    var z = Math.sqrt(x * x + y * y) + 0.00002 * Math.sin(y * this.X_PI);
    var theta = Math.atan2(y, x) + 0.000003 * Math.cos(x * this.X_PI);
    poi2.lng = z * Math.cos(theta) + 0.0065;
    poi2.lat = z * Math.sin(theta) + 0.006;
    return poi2;
};
/**
 * WGS->百度坐标系
 */
Convertor.prototype.WGS2BD09 = function (poi) {
    //WGS->GCJ
    var poi2 = this.WGS2GCJ(poi);
    if (typeof poi2 === "undefined") {
        return;
    }
    //GCJ->百度坐标系
    return this.GCJ2BD09(poi2);
}
/*-------------------本地原始坐标转其他坐标-----------------*/

/*---------------------------计算偏移角度----------------------------*/
//给出两个gps点获取地图上的车头方向返回偏移角度
//参数对应当前点,下个点,百度地图map实例
function setRotation(curPos, targetPos, map) {
    var me = this;
    var deg = 0;
    //这个方法安卓api没有,但根据方法名称可以推断出是将经纬度进行一样倍数的放大或缩小,也忘了
    //拿小数位比较多的double来计算对精度影响大不大,所以自己写了一个方法,直接将经纬度放大一百万倍
    curPos = map.pointToPixel(curPos);
    targetPos = map.pointToPixel(targetPos);
    if (targetPos.x != curPos.x) {
        var tan = (targetPos.y - curPos.y) / (targetPos.x - curPos.x),
            atan = Math.atan(tan);
        deg = atan * 360 / (2 * Math.PI);
        if (targetPos.x < curPos.x) {
            deg = -deg + 90 + 90;
        } else {
            deg = -deg;
        }
        deg = -deg;
    } else {
        var disy = targetPos.y - curPos.y;
        var bias = 0;
        if (disy > 0)
            bias = -1
        else
            bias = 1
        deg = -bias * 90;
    }
    return deg;
};
/*---------------------------计算偏移角度----------------------------*/

/*---------------------计算中心坐标和缩放级别------------------------*/
//根据地图的点计算中心坐标和缩放级别，并为地图设置中心坐标和缩放级别。  
//points是百度地图全是new point()的数组,此函数调用下方的getZoom()函数
function setZoom(points, map) {

    if (points.length > 0) {
        var maxLng = Math.max.apply(Math, points.forEach(function (o) { return o.lng }));
        var minLng = Math.min.apply(Math, points.forEach(function (o) { return o.lng }));
        var maxLat = Math.max.apply(Math, points.forEach(function (o) { return o.lat }));
        var minLat = Math.min.apply(Math, points.forEach(function (o) { return o.lat }));
        var res;
        for (var i = points.length - 1; i >= 0; i--) {
            res = points[i];
            if (res.lng > maxLng) maxLng = res.lng;
            if (res.lng < minLng) minLng = res.lng;
            if (res.lat > maxLat) maxLat = res.lat;
            if (res.lat < minLat) minLat = res.lat;
        };
        var cenLng = (parseFloat(maxLng) + parseFloat(minLng)) / 2;
        var cenLat = (parseFloat(maxLat) + parseFloat(minLat)) / 2;
        var zoom = this.getZoom(maxLng, minLng, maxLat, minLat, map);
        map.centerAndZoom(new BMap.Point(cenLng, cenLat), zoom);
    } else {
        //没有坐标，显示全中国  
        map.centerAndZoom(new BMap.Point(103.388611, 35.563611), 5);
    }
};
/*---------------------计算中心坐标和缩放级别------------------------*/

/*---------------------------计算缩放级别----------------------------*/
//计算缩放级别被 setZoom()调用,需要一起使用
function getZoom(maxLng, minLng, maxLat, minLat, map) {
    var zoom = ["50", "100", "200", "500", "1000", "2000", "5000", "10000", "20000", "25000", "50000", "100000", "200000", "500000", "1000000", "2000000"] //级别18到3。  
    //最大最小的坐标点
    var pointA = new BMap.Point(maxLng, maxLat); // 创建点坐标A  
    var pointB = new BMap.Point(minLng, minLat); // 创建点坐标B 
    var distance = map.getDistance(pointA, pointB).toFixed(1); //获取两点距离,保留小数点后两位 
    for (var i = 0, zoomLen = zoom.length; i < zoomLen; i++) {
        if (zoom[i] - distance > 0) {
            return 18 - i + 1;
            //地图范围常常是比例尺距离的10倍以上 所以加3
        }
    };
};

/*---------------------------计算缩放级别----------------------------*/
/*-------------------------------------------自定义ajax----------------------------------------------*/
//使用示例
//ajax({
//    url: '',
//    type: 'get',//不区分大小写
//    dataType: 'json',
//    data: { key:111},
//    async:true,
//    isStringfy: true,//是否将参数置为stringfy 
//    isParse: false, //是否将将回调数据格式化
//    setRequestHeader: [{ key: value }],
//    success: function (response, xml) {//请求成功后执行的代码},
//    error: function (status) {//失败后执行的代码} });
function ajax(options) {

    options = options || {};
    options.type = (options.type || 'GET').toUpperCase();
    options.dataType = options.dataType || 'json';

    //格式化参数
    var arr = [];
    for (var name in options.data) {
        arr.push(encodeURIComponent(name) + '=' + encodeURIComponent(options.data[name]));
    }
    var params = arr.join('&');

    options.isStringfy ? params = JSON.stringify(params) : '';
    options.async = options.async || true;
    //创建-第一步
    var xhr;
    //非IE6
    if (window.XMLHttpRequest) {
        xhr = new XMLHttpRequest();
    } else {
        //ie6及其以下版本浏览器
        xhr = ActiveXObject('Microsoft.XMLHTTP');
    }

    //接收-第三步
    xhr.onreadystatechange = function () {
        if (xhr.readyState == 4) {
            var status = xhr.status;
            if (status >= 200 && status < 300) {
                isParse ? xhr.responseText = JSON.parse(xhr.responseText) : "";
                options.success && options.success(xhr.responseText, xhr.responseXML);
            } else {
                options.error && options.error(status);
            }
        }
    }

    //连接和发送-第二步
    if (options.type == 'GET') {

        xhr.open('GET', options.url + '?' + params, options.async);
        if (options.setRequestHeader) {
            for (i = 0; i < options.setRequestHeader.length; i++) {
                //ie9以上支持
                xhr.setRequestHeader(Object.keys(options.setRequestHeader[i]), options.setRequestHeader[i][Object.keys(options.setRequestHeader[i])]);
            }
        }

        xhr.send(null);
    } else if (options.type == 'POST') {
        xhr.open('POST', options.url, options.async);
        //设置表单提交时的内容类型options.contentType "application/x-www-form-urlencoded"
        options.contentType ? "" : options.contentType = "application/x-www-form-urlencoded";
        xhr.setRequestHeader("Content-Type", options.contentType);
        if (options.setRequestHeader) {
            for (i = 0; i < options.setRequestHeader.length; i++) {
                //ie9以上支持
                xhr.setRequestHeader(Object.keys(options.setRequestHeader[i]), options.setRequestHeader[i][Object.keys(options.setRequestHeader[i])]);
            }
        }
        xhr.send(params);
    }
};
/*-------------------------------------------自定义ajax----------------------------------------------*/

