import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from "vuex-persistedstate";

Vue.use(Vuex);

const state = {
  targetLocation: null, //上次提交位置
  originAmbulLocation: null,//救护车首次定位
  originAmbulFlags:  //某个任务(guid)是否已经获取到车辆起始位置
    // 【这个车辆起始位置其实应该由后端返回，但是他们不肯做，说不好实现，所以只能在前端发起的第一次车辆位置调用返回的位置作为其实位置】
    { '87555a71-8725-4c91-a0d1-d598cbbeb09c': false },

};

const mutations = {
  setTargetLocation(state, location) {
    state.targetLocation = location;
  },
  setOriginAmbulLocation(state, location) {
    state.originAmbulLocation = location;
  },
  setOriginAmbulFlags(state, task) {
    state.originAmbulFlags[task.guid] =  task.flag;
  }
};

const getters = {

};

const actions = {

};

const store = new Vuex.Store({
  state,
  actions,
  mutations,
  getters,
  plugins: [createPersistedState()],
});

export default store