import axios from "axios";
import apiConfig from "../config";

export default function request(config) {
  const baseConfig = {
    baseURL: config.baseURL ||  apiConfig.baseURL,
    timeout: 5000,
  };

  const instance = axios.create(baseConfig);
  instance.interceptors.request.use(
    (request) => {
      //做修改后必须要返回
      return request;
    },
    (err) => {
      return Promise.reject(err);
    }
  );
  //接收响应拦截器
  instance.interceptors.response.use(
    (response) => {
      try {
        let idx = response.data.indexOf('<?xml version="1.0" encoding="utf-8"?>');
        if(idx!==-1){
          let resXML = response.data;
          let first = resXML.indexOf('{');
          let last = resXML.lastIndexOf('}');
          resXML = resXML.slice(first, last+1);
          return JSON.parse(resXML);
        }else{
          return JSON.parse(response.data);
        }
      } catch (error) {
        return response.data;
      }
    },
    (err) => {
      err && err.response
        ? (() => {
            switch (err.response.status) {
              case 400:
                err.message = "请求错误(400)";
                break;
              case 401:
                this.$router.push("/login");
                break;
              case 403:
                err.message = "拒绝访问(403)";
                break;
              case 404:
                err.message = "请求出错(404)";
                break;
              case 408:
                err.message = "请求超时(408)";
                break;
              case 500:
                err.message = "服务器错误(500)";
                break;
              case 501:
                err.message = "服务未实现(501)";
                break;
              case 502:
                err.message = "网络错误(502)";
                break;
              case 503:
                err.message = "服务不可用(503)";
                break;
              case 504:
                err.message = "网络超时(504)";
                break;
              case 505:
                err.message = "HTTP版本不受支持(505)";
                break;
              default:
                err.message = `连接出错(${err.response.status})!`;
            }
          })()
        : (err.message = "连接服务器失败!");
      return Promise.reject(err);
    }
  );
  return instance(config);
}
